import React from 'react';
import '../../styles/footer/ConditionsPage.css';

const PrivacyPolicyPage = () => {
  return (
    <div className="conditions-container">
      <h1>Personvern  Klkken Nettjenester</h1>

      <section className="conditions-section">
        <h2>Vrt engasjement for ditt personvern</h2>
        <p>
          Hos Klkken Nettjenester tar vi ditt personvern p alvor. Vi er forpliktet til  beskytte dine personlige
          opplysninger og gi deg full kontroll over hvordan de brukes. Vr personvernerklring beskriver hvordan vi samler
          inn, bruker og sikrer informasjonen din, i trd med gjeldende lover og forskrifter.
        </p>
      </section>

      <section className="conditions-section">
        <h2>Informasjon vi samler inn</h2>
        <p>
          Vi samler inn personopplysninger som navn, e-postadresse, og annen informasjon du oppgir nr du registrerer
          deg, oppdaterer profilen din, eller kommuniserer med andre medlemmer. Teknisk data, som IP-adresser og
          nettleserinformasjon, kan ogs bli samlet inn for  forbedre tjenesten.
        </p>
      </section>

      <section className="conditions-section">
        <h2>Hvordan vi bruker informasjonen</h2>
        <ul>
          <li>
            Til  levere og forbedre tjenestene vre, inkludert matchingsalgoritmer og brukeropplevelse.
          </li>
          <li>
            For  sikre sikkerheten til vre brukere gjennom profilkontroller og overvking av aktivitet.
          </li>
          <li>
            Til kommunikasjon, som varsler om nye meldinger, oppdateringer, og tilbud.
          </li>
          <li>
            For  overholde juridiske krav og beskytte mot svindel eller misbruk.
          </li>
        </ul>
      </section>

      <section className="conditions-section">
        <h2>Ditt samtykke og dine rettigheter</h2>
        <p>
          Ved  bruke vr tjeneste, gir du ditt samtykke til at vi kan behandle dine personopplysninger som beskrevet
          her. Du har rett til  be om innsyn, retting, eller sletting av dine data. Du kan ogs trekke tilbake ditt
          samtykke nr som helst ved  kontakte oss.
        </p>
      </section>

      <section className="conditions-section">
        <h2>Informasjonskapsler (Cookies)</h2>
        <p>
          Vi bruker informasjonskapsler for  forbedre brukeropplevelsen din. Disse hjelper oss med  analysere trafikk,
          tilpasse innhold, og lagre preferanser. Du kan administrere eller deaktivere informasjonskapsler via
          nettleserinnstillingene dine.
        </p>
      </section>

      <section className="conditions-section">
        <h2>Sikkerhet</h2>
        <p>
          Vi bruker avanserte sikkerhetstiltak for  beskytte informasjonen din mot uautorisert tilgang, tap eller
          misbruk. Dette inkluderer kryptering, brannmurer og kontinuerlige sikkerhetsoppdateringer.
        </p>
      </section>

      <section className="conditions-section">
        <h2>Kontakt oss</h2>
        <p>
          Har du sprsml om vr personvernerklring eller hvordan vi behandler dine opplysninger? Du kan kontakte oss
          p <a href="mailto:support@klaekken.no">support@klaekken.no</a>. Vi svarer s raskt som mulig for  adressere
          dine bekymringer.
        </p>
      </section>

      <section className="conditions-section">
        <h2>Oppdateringer av denne erklringen</h2>
        <p>
          Denne personvernerklringen kan bli oppdatert fra tid til annen for  reflektere endringer i lovgivning eller
          vre praksiser. Vi anbefaler at du regelmessig sjekker denne siden for oppdatert informasjon.
        </p>
      </section>
    </div>
  );
};

export default PrivacyPolicyPage;