import React from 'react';
import '../../styles/footer/ConditionsPage.css';

const ContactPage = () => {
  return (
    <div className="conditions-container">
      <h1>Kontakt oss  Klkken Nettjenester</h1>

      <section className="conditions-section">
        <h2>Vi er her for  hjelpe deg!</h2>
        <p>
          Hos Klkken Nettjenester er vi opptatt av at du skal f en best mulig opplevelse. Hvis du har sprsml, trenger hjelp,
          eller nsker  gi tilbakemelding, ikke nl med  ta kontakt med oss. Vi ser frem til  hre fra deg!
        </p>
      </section>

      <section className="conditions-section">
        <h2>Kontaktinformasjon</h2>
        <ul>
          <li><strong>E-post:</strong> <a href="mailto:support@klaekken.no">support@klaekken.no</a></li>
          <li><strong>Telefon:</strong> +47 123 45 678 (mandag til fredag, 09:0016:00)</li>
          <li><strong>Adresse:</strong> Klkken Nettjenester, 0123 Oslo, Norge</li>
        </ul>
      </section>

      <section className="conditions-section">
        <h2>Ofte stilte sprsml</h2>
        <p>
          Sjekk vr <a href="/faq">FAQ-side</a> for svar p de vanligste sprsmlene. Hvis du ikke finner svar der, kan du
          alltid kontakte oss direkte.
        </p>
      </section>

      <section className="conditions-section">
        <h2>Tilbakemelding</h2>
        <p>
          Vi verdsetter din mening! Hvis du har forslag til forbedringer eller nsker  dele din erfaring med oss, kan du sende
          en e-post til <a href="mailto:feedback@klaekken.no">feedback@klaekken.no</a>. Din tilbakemelding hjelper oss  gjre
          tjenesten enda bedre.
        </p>
      </section>

      <section className="conditions-section">
        <h2>Sosiale medier</h2>
        <p>
          Flg oss p sosiale medier for  holde deg oppdatert om nyheter, arrangementer, og spennende oppdateringer:
        </p>
        <ul>
          <li><a href="https://www.facebook.com/klaekken">Facebook</a></li>
          <li><a href="https://www.instagram.com/klaekken">Instagram</a></li>
          <li><a href="https://www.twitter.com/klaekken">Twitter</a></li>
        </ul>
      </section>

      <section className="conditions-section">
        <h2>Teknisk support</h2>
        <p>
          Opplever du tekniske problemer? Beskriv problemet ditt i detalj og kontakt oss via e-post p
          <a href="mailto:support@klaekken.no">support@klaekken.no</a>. Vi vil gjre vrt beste for  hjelpe deg s raskt som mulig.
        </p>
      </section>

      <section className="conditions-section">
        <h2>Personlig hjelp</h2>
        <p>
          Hvis du nsker personlig assistanse, kan du kontakte oss p telefon eller sende oss en melding via kontaktskjemaet
          p nettsiden. Vi er her for deg!
        </p>
      </section>
    </div>
  );
};

export default ContactPage;